.toolbar__toggle-button {
  display: none;
}
.nav-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding-top:3rem;
    background-color: #f6fdfa;
}

.logo{
    cursor: pointer;
    font-size: 50px;
    color: #052350;
    font-family: 'Great Vibes';
}

.logo:hover{
    color: #ee6e6c;
}

.nav_text:hover{
    background: transparent;
    color: #052350;
    font-size: 16px;
    border-radius: 5px;
    box-shadow: 0 2px 8px rgba(47, 72, 88, .30), 0 3px 3px rgba(47, 72, 88, .22);
}

.nav_links-container{
    list-style: none;
}

.nav_links, .nav_text, .cta{
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 16px;
    padding: .5rem 1rem;
    color: #052350;
    text-decoration: none;
}

.nav_links{
    display: inline-block;
    padding: 0 20px;
}

.nav_links:visited{
  background-color: #052350;
  color: white;
}

.nav_text, .logo{
    transition: all 0.3s ease-in-out 0s;
}

.cta{
    padding: 14px 40px;
    font-size: 16px;
    font-weight: 700;
    background: transparent;
    border: 1.3px solid #ee6e6c ;
    cursor: pointer;
    color: #ee6e6c;
    border-radius: 5px;
    transition: all 0.3s ease 0s;
    outline: none;
    margin-left: 1rem;
}

.cta:hover{
    /* color: #052350 !important;
    background: transparent !important;
    box-shadow: 0 2px 8px rgba(47, 72, 88, .30), 0 3px 3px rgba(47, 72, 88, .22) !important; */
    color: #ee6e6c !important;
    border: 1.3px solid transparent ;
    box-shadow: 0 2px 8px rgba(47, 72, 88, .30), 0 3px 3px rgba(47, 72, 88, .22);
}

/*Media Queries*/

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
    /* Styles */
    .cta{
      padding: 8px 12px;
      font-size: 16px;
      font-weight: 500;
      background: transparent;
      border: 1.3px solid #ee6e6c ;
      cursor: pointer;
      color: #ee6e6c;
      border-radius: 5px;
      transition: all 0.3s ease 0s;
      outline: none;
      margin-left: 1rem;
    }
    .nav-container {
      justify-content: space-between;
      padding: 0.8rem 1rem;
    }
    .nav_links-container {
      display: none;
    }
    .toolbar__toggle-button {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .toggle-button {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      height: 24px;
      width: 24px;
      background: transparent;
      border: none;
      cursor: pointer;
      padding: 0 0.25rem;
      box-sizing: border-box;
    }
  
    .toggle-button:focus {
      outline: none;
    }
  
    .toggle-button__line {
      width: 24px;
      height: 2px;
      background: #263238;
    }
  }
  
